html {
  position: relative;
  overflow-x: hidden;
}

body {
  position: relative;
  background: #f5f5f5;
  overflow-x: hidden;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 19px;
  box-sizing: content-box;
}

.title {
  font: 400 50px/1 "OfficinaSerifBookCTT", sans-serif;
  color: #222;
  text-align: center;
}
.title strong {
  font: 700 50px/1 "OfficinaSerifBoldC", sans-serif;
}
.title span {
  color: #ff9549;
}

.btn__blue {
  border-radius: 50px;
  padding: 12px;
  font: 700 20px "OfficinaSerifBoldC", sans-serif;
  text-align: center;
  color: #fff;
  background: linear-gradient(180deg, #0c8dce 0%, #38afeb 50.5%, #0e82bc 100%);
  border: none;
  transition: all 0.3s;
}
.btn__blue:hover {
  filter: brightness(80%);
}

.nav {
  background: #373737;
  position: relative;
}
.nav__row {
  position: relative;
  z-index: 3;
  padding: 14px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__row::before {
  content: "";
  display: block;
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background: url("../../img/line_bg_blc.jpg") top left repeat;
  background-size: 50px;
  z-index: -1;
  border-bottom: 1px solid rgba(255, 149, 73, 0.4);
}
.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 80%;
  width: 100%;
}
.nav__list a {
  font: 400 16px "Roboto", sans-serif;
  text-transform: uppercase;
  color: #ccc;
  transition: all 0.3s;
}
.nav__list a:hover {
  color: #ff9549;
}
.nav__list li {
  position: relative;
}
.nav__search {
  position: relative;
  width: 155px;
}
.nav__search input {
  box-sizing: border-box;
  width: 100%;
  padding: 9px 25px 9px 0;
  font: 400 16px "Roboto", sans-serif;
  color: #ccc;
  background: transparent;
  border: none;
}
.nav__search img {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.nav__flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 21px 0;
}
.nav__logo {
  width: 290px;
  gap: 13px;
  display: grid;
  grid-template-columns: 160px auto;
  align-items: center;
}
.nav__logo img {
  width: 100%;
}
.nav__logo p {
  font: 400 13px/1 "Roboto", sans-serif;
  color: #fff;
  opacity: 0.8;
}
.nav__logo p span {
  font: 600 13px/1 "Roboto", sans-serif;
}
.nav__menu {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 60px;
}
.nav__menu a {
  font: 500 16px "Roboto", sans-serif;
  text-transform: uppercase;
  color: #343434;
  position: relative;
  transition: all 0.3s;
}
.nav__menu a:hover {
  color: #ff9549;
}
.nav__menu a::before {
  content: "";
  display: block;
  width: 1px;
  height: 14px;
  background: #969696;
  position: absolute;
  right: -30px;
  top: 50%;
  transform: translateY(-50%);
}
.nav__menu li:last-child a::before {
  content: none;
}
.nav__menu_wrapper {
  padding: 4px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
  position: relative;
}
.nav__menu_wrapper::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  width: 100vw;
  height: 100%;
  background: #fff;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
  z-index: -1;
  transform: translateX(-50%);
}
.nav__catalog {
  position: relative;
}
.nav__catalog .drop__btn {
  font: 700 22px "OfficinaSerifBoldC", sans-serif;
  color: #fff;
  cursor: pointer;
  grid-template-columns: 20px auto;
  gap: 10px;
  padding: 10px 14px;
  border-radius: 6px;
  background: linear-gradient(180deg, #0c8dce 0%, #38afeb 50.5%, #0e82bc 100%);
}
.nav__catalog .drop__btn:after {
  display: none;
}
.nav__catalog .drop__btn i {
  display: flex;
  width: 20px;
  height: 17px;
  justify-content: space-between;
  flex-direction: column;
}
.nav__catalog .drop__btn i::before, .nav__catalog .drop__btn i::after,
.nav__catalog .drop__btn i span {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #fff;
}
.nav__fav {
  display: grid;
  grid-template-columns: 20px auto;
  gap: 10px;
  align-items: center;
  font: 400 16px "Roboto", sans-serif;
  color: #222;
}
.nav__baner {
  max-width: 241px;
  display: grid;
  grid-template-columns: 34px 1fr;
  gap: 14px;
  align-items: center;
}
.nav__baner img {
  width: 100%;
}
.nav__baner p {
  font: 400 16px/1.25 "OfficinaSerifBookCTT", sans-serif;
  color: #fff;
}
.nav__baner p span {
  color: #ff9549;
}
.nav__baner p strong {
  font: 700 16px/1.25 "OfficinaSerifBoldC", sans-serif;
}
.nav__soc {
  display: grid;
  grid-template-columns: 47px 47px;
  gap: 14px;
}
.nav__soc a {
  display: block;
}
.nav__soc img {
  width: 100%;
}
.nav__contact {
  display: grid;
  grid-template-columns: auto auto;
  gap: 14px;
}
.nav__contact_phone {
  font: 700 26px "OfficinaSerifBoldC", sans-serif;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-align: right;
  color: #fff;
  display: block;
  transition: all 0.3s;
}
.nav__contact_phone:hover {
  color: #ff9549;
}
.nav__contact_email {
  font: 400 16px "OfficinaSerifBookCTT", sans-serif;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-align: right;
  color: #fff;
  display: block;
  opacity: 0.8;
  transition: all 0.3s;
}
.nav__contact_email:hover {
  opacity: 1;
  color: #ff9549;
}

@media (max-width: 1200px) {
  .nav__row {
    padding: 10px;
  }
  .nav__list {
    max-width: calc(100% - 200px);
  }
  .nav__list a {
    font-size: 12px;
  }
  .nav__flex {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  .nav__baner {
    order: 2;
  }
  .nav__menu {
    gap: 30px;
  }
  .nav__menu a {
    font-size: 12px;
  }
  .nav__menu a::before {
    right: -15px;
  }
}
.header {
  padding: 60px 0 80px;
  position: relative;
  background-image: url("../../img/header_bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header h1 {
  font: 400 60px "OfficinaSerifBookCTT", sans-serif;
  color: #222;
}
.header h1 span {
  font: 700 60px "OfficinaSerifBoldC", sans-serif;
  color: #ff9549;
}
.header__info {
  max-width: 600px;
}
.header__text {
  margin: 20px 0 0;
  font: 400 28px "OfficinaSerifBookCTT", sans-serif;
  color: #222;
  opacity: 0.9;
}
.header__text span {
  font: 700 28px "OfficinaSerifBoldC", sans-serif;
}
.header__dir {
  margin: 60px 0 0;
  display: grid;
  grid-template-columns: 308px 1fr;
  gap: 20px;
  align-items: center;
}
.header__dir_sign {
  width: 206px;
}
.header__dir p {
  font: 400 18px "Roboto", sans-serif;
  color: #222;
  opacity: 0.6;
  margin: 14px 0 0;
}
.header__dir p + p {
  font: 400 28px "OfficinaSerifBookCTT", sans-serif;
  color: #222;
  opacity: 1;
  margin: 3px 0 0;
}

.produce {
  position: relative;
  background: #373737;
  padding: 90px 0 70px;
  overflow: hidden;
}
.produce h2 {
  font: 700 60px/0.9 "OfficinaSerifBoldC", sans-serif;
  text-align: center;
  color: #fff;
}
.produce__block {
  margin: 20px 0 0;
}
.produce__block + .produce__block {
  margin: 60px 0 0;
}
.produce__title {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 25px;
  align-items: center;
}
.produce__title_num {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
}
.produce__title_num p {
  font: 400 32px/0.9 "OfficinaSerifBookCTT", sans-serif;
  color: #fff;
}
.produce__title_num h3 {
  font: 700 45px/0.9 "OfficinaSerifBoldC", sans-serif;
  color: #ff9549;
}
.produce__title_vol {
  padding: 0 0 5px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.7019607843);
}
.produce__title_vol p {
  font: 400 20px "OfficinaSerifBookCTT", sans-serif;
  color: #fff;
  opacity: 0.7;
  text-align: right;
}
.produce__title_arrow {
  display: grid;
  grid-template-columns: 45px 45px;
  gap: 14px;
}
.produce__title_arrow .swiper-button-prev,
.produce__title_arrow .swiper-button-next {
  position: relative;
  margin: 0;
  width: 45px;
  height: 45px;
  inset: auto;
  transform: none;
  border: 2px solid #7a7a7a;
  border-radius: 8px;
  opacity: 1;
  background: #fff;
}
.produce__title_arrow .swiper-button-prev::after,
.produce__title_arrow .swiper-button-next::after {
  content: none;
}
.produce__title_arrow .swiper-button-next {
  transform: rotate(180deg);
}
.produce__slider {
  margin: 30px 0 0;
  overflow: visible;
}
.produce__slider .swiper-pagination {
  position: relative;
  width: 100%;
  margin: 40px 0 0;
  transform: none;
  inset: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.produce__slider .swiper-pagination span {
  margin: 0 !important;
  width: 10px;
  height: 10px;
  background: #fff;
  opacity: 1;
  transition: all 0.3s;
}
.produce__slider .swiper-pagination span.swiper-pagination-bullet-active {
  background: #ff9549;
}
.produce__slide {
  padding: 6px 6px 22px;
  border-radius: 13px;
  background: #fff;
  opacity: 0.3;
  transition: all 0.3s;
}
.produce__slide img {
  width: 100%;
  border-radius: 10px;
}
.produce__slide p {
  margin: 17px 10px 0;
  font: 400 30px/0.9 "OfficinaSerifBookCTT", sans-serif;
  color: #222;
}
.produce__slide.swiper-slide-visible {
  opacity: 1;
}

.step {
  padding: 70px 0 0;
}
.step__wrapper {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 6px;
}
.step__item {
  border-radius: 20px;
  background: #373737;
  position: relative;
  padding: 10px 6px 6px;
  display: flex;
  flex-direction: column;
}
.step__title {
  display: grid;
  grid-template-columns: 40px 1fr 40px;
  gap: 5px;
  align-items: center;
  margin: 0 0 auto;
  min-height: 52px;
}
.step__title img {
  width: 40px;
  height: 40px;
  object-fit: contain;
}
.step__num {
  font: 700 32px/1 "Roboto", sans-serif;
  color: #ff9549;
}
.step__name {
  font: 400 26px/1 "OfficinaSerifBookCTT", sans-serif;
  color: #fff;
}
.step__text {
  border-radius: 18px;
  background: #fff;
  min-height: 196px;
  padding: 20px;
  margin: 20px 0 0;
  position: relative;
  z-index: 1;
}
.step__text p {
  font: 400 16px/1.2 "Roboto", sans-serif;
  color: #222;
  opacity: 0.8;
}
.step__text img {
  position: absolute;
  width: 130px;
  height: 130px;
  object-fit: contain;
  bottom: 10px;
  right: 10px;
  z-index: -1;
}
.step__phone {
  grid-column: 3/5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  background: url(../../img/line_bg_blc.jpg) top left;
  background-size: 50px;
  border-radius: 20px;
}
.step__phone_row {
  display: grid;
  grid-template-columns: 99px auto;
  gap: 23px;
  align-items: start;
}
.step__phone_row img {
  width: 100px;
  height: 100px;
}
.step__phone_row p {
  font: 700 40px/1.1 "OfficinaSerifBoldC", sans-serif;
  color: #fff;
}
.step__phone_row p span {
  font: 400 40px/1.1 "OfficinaSerifBookCTT", sans-serif;
  color: #ff9549;
  display: block;
}
.step__ban {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 25px;
  margin: 40px 0 0;
  width: 100%;
}
.step__ban p {
  font-weight: 700;
}

@media (max-width: 1200px) {
  .step__wrapper {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .step__phone {
    grid-column: 2/4;
  }
}
.order {
  padding: 70px 0 0;
}
.order__bg {
  margin: -112px 0 0;
}

.consul {
  padding: 55px 0 50px;
  position: relative;
  background: #373737;
  z-index: 1;
}
.consul h2 {
  font: 700 60px/0.95 "OfficinaSerifBoldC", sans-serif;
  color: #fff;
}
.consul h2 span {
  color: #ff9549;
}
.consul__subtitle {
  font: 400 36px/0.95 "OfficinaSerifBookCTT", sans-serif;
  color: #fff;
  opacity: 0.7;
  margin: 8px 0 0;
}
.consul__link {
  margin: 44px 0 0;
  max-width: 630px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 15px;
}
.consul__link a {
  border: 2px solid #ff9549;
