.drop {
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  width: max-content;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: hidden;
  opacity: 0;
  transition: all 0.3s;
  pointer-events: none;
  z-index: 5;
}
.drop a {
  background: #fff;
  font: 500 14px "Roboto", sans-serif;
  text-transform: none;
  color: #343434;
  padding: 10px;
  display: block;
  transition: all 0.3s;
}
.drop a:hover {
  background: #f5f5f5;
  color: #ff9549;
}
.drop.active {
  opacity: 1;
  pointer-events: auto;
}
.drop__btn {
  display: grid;
  grid-template-columns: auto 16px;
  gap: 14px;
  align-items: center;
}
.drop__btn::after {
  content: "";
  display: block;
  width: 16px;
  height: 10px;
  background: url("../../img/drop-arrow.svg") center no-repeat;
  background-size: contain;
  transition: all 0.3s;
}
.drop__btn.active::after {
  transform: rotate(-180deg);
}